const mapState = (state) => {
  return {
    url_base: state.url_base,
    agentdata: state.agentdata,
    callstate: state.callstate,
    userid: state.userid,
    break_reasons: state.break_reasons,
    callstate: state.callstate,
  };
};
const mapDis = (dispatch) => {
  return {  changeNavigate: (item, state) =>
    dispatch(actions.changeNavigate(item, state)),};
};


export default withRouter(connect(mapState, mapDis)(EmailView));
