import React from "react";

class FileMessage extends React.Component {
  render() {
    let { rcocClient, message } = this.props;
    if (message.fromCustomer) {
      return (
        <>
          <div class="message stark" style={{ fontFamily: "Vazir" }}>
            <span>
              {message.text.replace("file/", "")}
              <ion-icon
                style={{ color: "#fff", fontSize: 19, marginTop: 2 }}
                name="cloud-download-outline"
              ></ion-icon>
            </span>
            <span style={{ fontSize: 10 }} class="time_date">
              {message.date}
            </span>
          </div>
        </>
      );
    } else {
      return (
        <>
          <div class="message parker" style={{ fontFamily: "Vazir" }}>
            <span>
              {message.text.replace("file/", "")}
              <ion-icon
                style={{ color: "#fff", fontSize: 19, marginTop: 2 }}
                name="cloud-download-outline"
              ></ion-icon>
            </span>
            <span style={{ fontSize: 10 }} class="time_date">
              {message.date}
            </span>
          </div>
        </>
      );
    }
  }
}

export default FileMessage;
