import React from "react";
import { Route, Link, Redirect, withRouter } from "react-router-dom";
import { Button, Image, StyleSheet, Text, View, TextInput } from "react-native";
import { connect } from "react-redux";
import { Tabs } from "antd-mobile";

import {
  setAgentData,
  setSessions,
  changeNavigate,
  setActiveChatSession,
  popCall,
} from "../../../redux/action";
import Timer from "../../Timer";

let timer;
let lastState;
class VoiceSessionTabButton extends React.Component {
  onClickFunc;

  componentDidMount() {
    let { session } = this.props;
    let { start } = session;
    lastState = session.state;
    let timerEl = document
      .getElementById(this.props.session.sessionId)
      .querySelector('span[name="dur"]');
    if (timer) {
      timer.resetElement(timerEl, start);
      timer.renderTimer();
    } else {
      timer = new Timer(timerEl, { start: start });
      timer.start();
    }
  }

  componentWillUnmount() {}

  componentDidUpdate() {
    let { state } = this.props.session;
    if (lastState.id !== state.id) {
      lastState = state;
      timer.start();
    }
  }

  render() {
    return (
      <>
        <div
          // onClick={this.props.onClickFunc}
          id={this.props.session.sessionId}
          name={this.props.session.endpoint}
          className="d-flex justify-content-between mt-3"
        >
          <div class="d-flex" align="left" name="md_ic">
            <div>
              <ion-icon
                onClick={() => this.props.popCall(true)}
                style={{
                  marginTop: 8,
                  fontSize: 22,
                  marginLeft: -5,
                  marginRight: 15,
                }}
                name="ellipsis-vertical-outline"
              ></ion-icon>
            </div>
            <div>
              <span
                name="md_ic"
                name="dur"
                style={{
                  fontFamily: "Vazir",
                }}
              >
                00:00:00
              </span>
              <h6 style={{ fontSize: 10, fontFamily: "Vazir", color: "#666" }}>
                {" "}
                <span
                  name="state"
                  style={{
                    fontWeight: 600,
                    fontFamily: "Vazir",
                    float: "left",
                    color: this.props.session.state.backColor,
                  }}
                >
                  {this.props.session.state.text}
                  {this.props.session.typingState
                    ? this.chatStates[this.props.session.typingState]
                    : ""}
                </span>
              </h6>
            </div>
          </div>

          <div
            className="d-flex"
            align="right"
            onClick={() => {
              this.props.changeNavigate("CallView", this.props.callstate);
            }}
          >
            <div style={{ marginRight: 10 }}>
              <h6>{this.props.session.number}</h6>
              <h6 style={{ fontSize: 13 }}>
                {" "}
                <span name="campaign">
                  {this.props.session.campaign}
                  {this.props.session.queue ? (
                    <span name="queue">({this.props.session.queue})</span>
                  ) : (
                    ""
                  )}
                </span>
              </h6>
            </div>
            <div
              align="center"
              style={{
                height: 45,
                width: 45,
                borderBottom: "1px solid #eee",
                borderRadius: 40,

                backgroundColor: "#5ae07e",
              }}
            >
              <a
                style={{
                  color: "#fff",
                  fontSize: 24,
                  paddingTop: 19,
                }}
              >
                A
              </a>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapState = (state) => {
  return {
    url_base: state.url_base,
    navigate: state.navigate,
    callstate: state.callstate,
    popcall: state.popcall,
    sessions: state.sessions.filter(
      (session) => session.mediaType.toLowerCase() === "chat"
    ),
  };
};
const mapDis = (dispatch) => {
  return {
    setActiveChatSession: (session) => dispatch(setActiveChatSession(session)),
    popCall: (val) => dispatch(popCall(val)),
    changeNavigate: (item, state, userid, propsObj) =>
      dispatch(changeNavigate(item, state, userid, propsObj)),
  };
};

export default withRouter(connect(mapState, mapDis)(VoiceSessionTabButton));
