const mapState = (state) => {
  return {
    url_base: state.url_base,
    navigate: state.navigate,
    callstate: state.callstate,
    popchat: state.popchat,
    sessions: state.sessions.filter(
      (session) => session.mediaType.toLowerCase() === "chat"
    ),
  };
};
const mapDis = (dispatch) => {
  return {
    setActiveChatSession: (session) => dispatch(setActiveChatSession(session)),
    popChat: (val) => dispatch(popChat(val)),
    changeNavigate: (item, state, userid, propsObj) =>
      dispatch(changeNavigate(item, state, userid, propsObj)),
  };
};

export default withRouter(connect(mapState, mapDis)(Inbound));
