import React from "react";
import { Button, Image, StyleSheet, Text, View, TextInput } from "react-native";
import { Tabs, Input, List } from "antd-mobile";
import RcocClient from "../../../RcocClient";

class Emails extends React.Component {
  state = { smsadd: [] };
  handle_sms_customer_number_input(e) {
    this.setState({
      sms_customer_number: e.target.value,
    });
  }
  iframe() {
    return {
      __html: this.props.iframe,
    };
  }
  handle_sms_text(e) {
    this.setState({
      sms_text: e.target.value,
    });
  }
  handle_add_sms_number() {
    this.setState((prevState) => {
      let newData_list = [...prevState.smsadd];
      newData_list.push(this.state.sms_customer_number);
      return { smsadd: newData_list };
    });
  }
  handle_update_sms_number(index, e) {
    const ddd = this.state.smsadd;
    ddd[index] = e.target.value;
    this.setState({ smsadd: ddd });
  }
  handle_remove_sms_number(index) {
    this.setState((prevState) => {
      let newData_list = [...prevState.smsadd];
      newData_list.splice(index, 1);
      return { smsadd: newData_list };
    });
  }

  Iframe(props) {
    return (
      <div
        dangerouslySetInnerHTML={{ __html: props.iframe ? props.iframe : "" }}
      />
    );
  }

  onClickUploadAttachment(e) {
    let input = e.target.parentElement.querySelector('input[type="file"]');
    input.click();
  }

  uploadFile(e) {
    if (e.target.files.length === 0) return;

    let { session, rcocClient } = this.props;
    let file = e.target.files[0];
    rcocClient.email.uploadFile(file, session);
  }

  onReplyClicked(e) {
    let { session, rcocClient } = this.props;
    let container = document.getElementById(session.sessionId + "_container");
    let body = container.querySelector('textarea[name="body"').value;

    let atcs = [];
    rcocClient.email.reply(session, body, atcs);
  }

  render() {
    let { session, rcocClient } = this.props;
    return (
      <>
        <div class="container" id={session.sessionId + "_container"}>
          <div class="row">
            <div class="col-md-12 mt-1" align="right">
              <h6 style={{ fontFamily: "Vazir", marginTop: 20 }}>
                {session.data.From} : ارسال کننده
              </h6>
              <h6 style={{ fontFamily: "Vazir" }}>
                موضوع : {session.data.subject}
              </h6>

              <h6 style={{ fontFamily: "Vazir" }}>
                ارسال به : {session.data.To}
              </h6>
              <div
                dangerouslySetInnerHTML={{
                  __html:
                    '<iframe width="100%" height="166" scrolling="no" frameborder="no" allow="autoplay" src="' +
                    session.data.Body +
                    '"></iframe>',
                }}
              />
              {session.data.Attachments?.map((atc, index) => {
                let atcName = atc.substr(session.data.AttachmentsUrl.length);
                return (
                  <a
                    onClick={() => rcocClient.email.openAtc(atc)}
                    style={{
                      color: "#0080ff",
                      fontFamily: "Vazir",
                      marginBottom: 10,
                    }}
                  >
                    {atcName}
                    <ion-icon
                      name="attach-outline"
                      onClick={() => rcocClient.email.downloadAtc(atc)}
                      style={{
                        color: "#0080ff",

                        fontSize: 20,
                      }}
                    ></ion-icon>
                  </a>
                );
              })}
            </div>
            <hr />
            <div class="col-md-12 mt-3" align="right">
              <div class="form-group">
                <textarea
                  name="body"
                  rows="6"
                  class="form-control"
                  style={{ fontFamily: "Vazir" }}
                ></textarea>
              </div>
              <div class="form-group">
                <div
                  style={{
                    backgroundColor: "#f8f8f8",
                    borderRadius: 6,
                    height: 50,
                    border: "1px solid #ccc",
                  }}
                >
                  <div class="to-atcs">
                    {this.props.session.atcs?.map((atc, index) => {
                      if (atc.upload === true)
                        return (
                          <div class="d-flex justify-content-between">
                            <div class="d-flex" style={{}}>
                              {" "}
                              <div
                                align="center"
                                style={{
                                  width: 35,
                                  height: 35,
                                  background: "#ccc",
                                  borderRadius: 4,
                                  marginLeft: 7,
                                  marginTop: 7,
                                }}
                              >
                                <ion-icon
                                  style={{
                                    fontSize: 22,
                                    marginTop: 6,
                                  }}
                                  onClick={(e) =>
                                    rcocClient.email.removeFromFileFromAttachments(
                                      session.sessionId,
                                      index
                                    )
                                  }
                                  name="close-outline"
                                ></ion-icon>
                              </div>
                              <div
                                align="center"
                                style={{
                                  width: 35,
                                  height: 35,
                                  background: "#ccc",
                                  borderRadius: 4,
                                  marginLeft: 7,
                                  marginTop: 7,
                                }}
                              >
                                <ion-icon
                                  style={{
                                    fontSize: 22,
                                    marginTop: 6,
                                  }}
                                  onClick={(e) =>
                                    rcocClient.email.openAtc(atc.url)
                                  }
                                  name="eye-outline"
                                ></ion-icon>
                              </div>
                            </div>
                            <h6 style={{ margin: 15 }}> {atc.name}</h6>
                          </div>
                        );
                      else
                        return (
                          <div class="d-flex justify-content-between">
                            <div class="d-flex" style={{ margin: 25 }}>
                              <ion-icon
                                onClick={(e) =>
                                  rcocClient.email.openAtc(atc.url)
                                }
                                name="eye-outline"
                              ></ion-icon>
                              <ion-icon
                                onClick={(e) =>
                                  rcocClient.email.re_addNewUploadedFileToAttachments(
                                    session.sessionId,
                                    index
                                  )
                                }
                                name="add-outline"
                              ></ion-icon>
                            </div>
                            <h6 style={{ margin: 15 }}> {atc.name}</h6>
                          </div>
                        );
                    })}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div
          class="footer-menu d-flex"
          align="center"
          style={{
            height: 70,
            backgroundColor: "#6f0f4f",
            boxShadow: "0px 2px 21px 2px rgba(235,235,235,0.75)",

            borderTop: "1px solid #eee",

            overflow: "hidden",
            position: "fixed",
            width: "100%",

            margin: "0px auto",
            bottom: 50,
          }}
        >
          <a
            onClick={(e) => this.onReplyClicked(e)}
            class="btn btn-small btn-primary btn-block"
            style={{
              marginTop: 6,
              color: "#fff",
              fontFamily: "Vazir",
              backgroundColor: "#6f0f4f",
              border: "1px solid #6f0f4f",
            }}
          >
            ارسال
          </a>
          <input type="file" onChange={(e) => this.uploadFile(e)} hidden />
          <a
            onClick={this.onClickUploadAttachment}
            class="btn btn-small btn-primary btn-block"
            style={{
              marginTop: 6,
              color: "#fff",
              fontFamily: "Vazir",
              backgroundColor: "#6f0f4f",
              border: "1px solid #6f0f4f",
            }}
          >
            فایل
            <ion-icon
              name="attach-outline"
              style={{
                color: "#fff",
              }}
            ></ion-icon>
          </a>
        </div>
      </>
    );
  }
}

export default Emails;
