import React from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import CRM from "./CallView/CRM";
import History from "./CallView/History";
import Phonebook from "../Phonebook";
import * as actions from "./../../../redux/action";
class CallView extends React.Component {
  state = {
    tab: 1,
  };
  render() {
    let { rcocClient, session } = this.props;
    return (
      <>
        <section style={{ height: "100vh", backgroundColor: "#fff" }}>
          <section
            style={{
              backgroundColor: "#6f0f4f",
              height: this.props.callstate !== "" ? 120 : 100,
            }}
          >
            <div class="container">
              <div class="row">
                <div class="col-md-12" align="right">
                  <div class="d-flex justify-content-between">
                    <div class="d-flex">
                      <div style={{backgroundColor:'#561042',height:30,width:30,       marginTop: 19,
                          marginRight: 10,borderRadius:10}}>  <ion-icon
                      onClick={() =>
                        this.props.changeNavigate("Calls", this.props.callstate)
                      }
                        style={{
                          color: "#fff",
                          fontSize: 19,
                          marginRight:6,marginTop:6
                   
                        }}
                        name="arrow-back-outline"
                      ></ion-icon></div>
                     <div style={{backgroundColor:'#561042',height:30,width:30,       marginTop: 19,
                          marginRight: 10,borderRadius:10}}>    <ion-icon
                          onClick={() =>
                            rcocClient.commands.transactionComplete(session)
                          }
                          style={{ color: "#fff", fontSize: 19,   marginRight:6,marginTop:6 }}
                          name="stop"
                        ></ion-icon> </div>
                   
                   <div style={{backgroundColor:'#561042',height:30,width:30,       marginTop: 19,
                          marginRight: 10,borderRadius:10}}> 
                           <ion-icon
                        onClick={() => {
                          rcocClient.commands.getPhonebook();
                          this.setState({ phonebookVisible: true });
                        }}
                        style={{ color: "#fff", fontSize: 19,   marginRight:6,marginTop:6 }}
                        name="repeat"
                      ></ion-icon>
                             </div>
                     
                    </div>
                    <h1
                      style={{
                        fontSize: 24,
                        marginTop: this.props.callstate !== "" ? 40 : 20,
                        marginRight: 6,
                        fontFamily: "Vazir",
                        color: "#fff",
                      }}
                    >
                      تاریخچه کاربر
                    </h1>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-md-12" align="right">
                  <div class="d-flex" style={{ flexDirection: "row-reverse" }}>
                    {(() => {
                      if (this.state.tab === 1) {
                        return (
                          <div
                            style={{
                              height: 30,
                              marginLeft: 10,
                              borderRadius: 30,
                              width: "auto",
                              backgroundColor: "#561042",
                            }}
                          >
                            <h6
                              style={{
                                fontFamily: "Vazir",
                                color: "#fff",
                                paddingLeft: 20,
                                paddingRight: 20,
                                marginTop: 6,
                                fontSize: 13,
                              }}
                            >
                              CRM
                            </h6>
                          </div>
                        );
                      } else {
                        return (
                          <div
                            onClick={() => this.setState({ tab: 1 })}
                            style={{
                              height: 30,
                              marginLeft: 10,
                              borderRadius: 30,
                              width: "auto",
                              //backgroundColor: "#8a165a",
                            }}
                          >
                            <h6
                              style={{
                                fontFamily: "Vazir",
                                color: "#fff",
                                paddingLeft: 20,
                                paddingRight: 20,
                                marginTop: 6,
                                fontSize: 13,
                              }}
                            >
                              CRM
                            </h6>
                          </div>
                        );
                      }
                    })()}
                    {(() => {
                      if (this.state.tab === 2) {
                        return (
                          <div
                            style={{
                              height: 30,
                              marginLeft: 10,
                              borderRadius: 30,
                              width: "auto",
                              backgroundColor: "#561042",
                            }}
                          >
                            <h6
                              style={{
                                fontFamily: "Vazir",
                                color: "#fff",
                                paddingLeft: 20,
                                paddingRight: 20,
                                marginTop: 6,
                                fontSize: 13,
                              }}
                            >
                              تاریخچه
                            </h6>
                          </div>
                        );
                      } else {
                        return (
                          <div
                            onClick={() => this.setState({ tab: 2 })}
                            style={{
                              height: 30,
                              marginLeft: 10,
                              borderRadius: 30,
                              width: "auto",
                              //backgroundColor: "#8a165a",
                            }}
                          >
                            <h6
                              style={{
                                fontFamily: "Vazir",
                                color: "#fff",
                                paddingLeft: 20,
                                paddingRight: 20,
                                marginTop: 6,
                                fontSize: 13,
                              }}
                            >
                              تاریخچه
                            </h6>
                          </div>
                        );
                      }
                    })()}
                  </div>
                </div>
              </div>
            </div>
          </section>
          {(() => {
            if (this.state.tab === 1) {
              return <CRM />;
            } else if (this.state.tab === 2) {
              return <History />;
            }
          })()}
        </section>
      </>
    );
  }
}

const mapState = (state) => {
  return {
    url_base: state.url_base,
    userid: state.userid,
    rcocClient: window.rcc,
    phonebook: state.phonebook,
    callstate: state.callstate,
  };
};
const mapDis = (dispatch) => {
  return {  changeNavigate: (item, state) =>
    dispatch(actions.changeNavigate(item, state)),};
};

export default withRouter(connect(mapState, mapDis)(CallView));
