const mapState = (state) => {
  return {
    navigate: state.navigate,
    url_base: state.url_base,
    callstate: state.callstate,
    sessions: state.sessions,
    popcall: state.popcall,
  };
};
const mapDis = (dispatch) => {
  return {
    changeNavigate: (item) => dispatch(changeNavigate(item)),
    popCall: (val) => dispatch(popCall(val)),
  };
};

export default withRouter(connect(mapState, mapDis)(Inbound));
