import React from "react";
import { Image, StyleSheet, Text, View, TextInput } from "react-native";
import { Tabs } from "antd-mobile";
import { Drawer } from "antd";

import Calls from "./Calls";
import Chats from "./Chats";
import EndPoints from "./Endpoints";
import Emails from "./Emails";
import Faxs from "./Faxs";
import Dial from "./Dial";

class App extends React.Component {
  state = {
    navigate: "Calls",
    visible: false,
    placement: "right",
  };
  showDrawer() {
    this.setState({
      visible: true,
    });
  }

  onClose() {
    this.setState({
      visible: false,
    });
  }

  onChange(e) {
    this.setState({
      placement: e.target.value,
    });
  }

  render() {
    return (
      <>
        <Drawer
          title={
            <h6
              style={{
                fontFamily: "Vazir",
                textAlign: "right",
                marginLeft: 120,
                paddingTop: 15,
                color: "#fff",
              }}
            >
              حساب کاربری
            </h6>
          }
          push={true}
          keyboard={true}
          headerStyle={{ height: 60, backgroundColor: "#6f0f4f" }}
          placement={this.state.placement}
          closable={false}
          onClose={() => this.onClose()}
          visible={this.state.visible}
          key={this.state.placement}
        >
          <div align="right">
            <p>منو اول</p>
            <p>منو دوم</p>
            <p>منو سوم</p>
          </div>
        </Drawer>

        <section style={{ height: "100vh", backgroundColor: "#fff" }}>
          <section style={{ backgroundColor: "#6f0f4f", height: 65 }}>
            <div class="container">
              <div class="row">
                <div class="col-md-12">
                  <div class="d-flex justify-content-between">
                    <div>
                      <img
                        style={{
                          border: "2px solid #fff",
                          height: 50,
                          width: 50,
                          marginTop: 16,
                          marginLeft: 5,
                          borderRadius: 30,
                          zIndex: -1,
                        }}
                        src={
                          "https://thumbor.forbes.com/thumbor/960x0/https%3A%2F%2Fspecials-images.forbesimg.com%2Fimageserve%2F61688aa1d4a8658c3f4d8640%2FAntonio-Juliano%2F0x0.jpg%3Ffit%3Dscale"
                        }
                      />

                      <div
                        style={{
                          height: 20,
                          width: 20,
                          borderRadius: 20,
                          marginTop: -20,
                          position: "absolute",
                          backgroundColor: "#21de50",
                        }}
                      ></div>
                    </div>

                    <a onClick={() => this.showDrawer()}>
                      <ion-icon
                        name="menu"
                        style={{ fontSize: 38, marginTop: 20, color: "#fff" }}
                      ></ion-icon>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </section>

          {/* components return with enu and tabs here */}
          {(() => {
            if (this.state.navigate === "Calls") {
              return <Dial />;
            } else if (this.state.navigate === "EndPoints") {
              return <EndPoints />;
            } else if (this.state.navigate === "Chats") {
              return <Chats />;
            } else if (this.state.navigate === "Emails") {
              return <Emails />;
            } else if (this.state.navigate === "Faxs") {
              return <Faxs />;
            }
          })()}
          <div
            align="center"
            style={{
              height: 60,
              boxShadow: "0px 2px 21px 2px rgba(235,235,235,0.75)",

              borderTop: "1px solid #eee",
              backgroundColor: "#fff",
              overflow: "hidden",
              position: "fixed",
              borderRadius: 15,
              margin: "0px auto",
              bottom: 20,

              right: 20,
              left: 20,
            }}
          >
            <div className="d-flex justify-content-between">
              <a onClick={() => this.setState({ navigate: "Faxs" })}>
                {(() => {
                  if (this.state.navigate === "Faxs") {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26, color: "#6f0f4f" }}
                        name="document"
                      ></ion-icon>
                    );
                  } else {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26 }}
                        name="document-outline"
                      ></ion-icon>
                    );
                  }
                })()}
              </a>
              <a onClick={() => this.setState({ navigate: "Emails" })}>
                {(() => {
                  if (this.state.navigate === "Emails") {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26, color: "#6f0f4f" }}
                        name="mail"
                      ></ion-icon>
                    );
                  } else {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26 }}
                        name="mail-outline"
                      ></ion-icon>
                    );
                  }
                })()}
              </a>
              <a onClick={() => this.setState({ navigate: "Chats" })}>
                {(() => {
                  if (this.state.navigate === "Chats") {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26, color: "#6f0f4f" }}
                        name="chatbubble"
                      ></ion-icon>
                    );
                  } else {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26 }}
                        name="chatbubble-outline"
                      ></ion-icon>
                    );
                  }
                })()}
              </a>
              <a onClick={() => this.setState({ navigate: "EndPoints" })}>
                {(() => {
                  if (this.state.navigate === "EndPoints") {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26, color: "#6f0f4f" }}
                        name="layers"
                      ></ion-icon>
                    );
                  } else {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26 }}
                        name="layers-outline"
                      ></ion-icon>
                    );
                  }
                })()}
              </a>
              <a onClick={() => this.setState({ navigate: "Calls" })}>
                {(() => {
                  if (this.state.navigate === "Calls") {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26, color: "#6f0f4f" }}
                        name="call"
                      ></ion-icon>
                    );
                  } else {
                    return (
                      <ion-icon
                        style={{ margin: 17, fontSize: 26 }}
                        name="call-outline"
                      ></ion-icon>
                    );
                  }
                })()}
              </a>
            </div>
          </div>
        </section>
      </>
    );
  }
}

const styles = StyleSheet.create({
  app: {
    marginHorizontal: "auto",
    maxWidth: 500,
    backgroundColor: "#af1a72",
    height: "100vh",
  },
  logo: {
    marginTop: 20,
    height: 190,
  },
  header: {
    padding: 20,
    backgroundColor: "#af1a72",
  },
  title: {
    fontWeight: "bold",
    fontSize: "1.5rem",
    marginVertical: "1em",
    textAlign: "center",
  },
  text: {
    lineHeight: "1.5em",
    fontSize: "1.125rem",
    marginVertical: "1em",
    textAlign: "center",
  },
  link: {
    color: "#1B95E0",
  },
  code: {
    fontFamily: "monospace, monospace",
  },
});

export default App;
