import * as types from "./type";
export const callAction = () => {
  return {
    type: types.CALL,
  };
};
export const changeNavigate = (val, state, userid, propsObj) => {
  return {
    type: types.CHANGE_NAVIGATE,
    payload: val,
    payloadx: state,
    payloady: userid,
    payloadProps: propsObj,
  };
};

