import moment from 'moment';

export default class TimerUp {
    options
    offset;
    clock;
    interval;
    timer;

    constructor(_timer, options){
        this.timer = _timer;
        // default options
        this.options = options || {};
        this.options.delay = this.options.delay || 1000;
    }

    start() {
        this.reset();
        if (!this.interval) {
            this.offset = this.options.start || Date.now();
            this.interval = setInterval(() => this.update(), this.options.delay);
        }
    }

    stop() {
        if (this.interval) {
            clearInterval(this.interval);
            this.interval = null;
        }
    }

    reset() {
        this.clock = 0;
        this.renderTimer();
    }

    update() {
        this.clock += this.delta();
        this.renderTimer();
    }

    resetElement(_timer){
        this.timer = _timer;
    }

    renderTimer() {
        let m = moment(this.clock);
        m.utc();
        this.timer.innerHTML = m.format("HH:mm:ss");
    }

    delta() {
        var now = Date.now(),
            d = now - this.offset;

            this.offset = now;
        return d;
    }
};