import React from "react";
import axios from "axios";
import { Modal, Tabs, Avatar } from "antd";
import {} from "antd-mobile";
const { TabPane } = Tabs;
var token = localStorage.getItem("token");

class Phonebook extends React.Component {
  componentDidMount() {
    this.getQueues();
  }

  componentWillUnmount() {}
  componentDidUpdate() {}

  getQueues() {
    axios
      .get(`${this.props.url_base}/Api/v1/cm/agentdesktop/queues`, {
        headers: { Authorization: `Bearer ${token}` },
      })
      .then((res) => {
        console.log("queues ", res);
        this.setState({
          queues: res.data,
        });
      })
      .catch((error) => {
        this.error_handle("مشکلی در ارتباط با سرور به وجود امده است");
        return error;
      });
  }

  state = {
    queues: [],
    selected: null,
  };

  onSelectedAgent(agent) {
    let isBlind = false;
    let isConference = false;
    this.props.transferRequest(
      this.props.endpoint.defaultCampaign,
      this.props.endpoint.name,
      null,
      agent.ExtNumber,
      null,
      isBlind,
      isConference,
      75
    );
    this.closePhonebook();
  }

  onSelectedQueue(queue) {
    let isBlind = false;
    let isConference = false;
    this.props.transferRequest(
      this.props.endpoint.defaultCampaign,
      this.props.endpoint.name,
      null,
      null,
      queue.name,
      isBlind,
      isConference,
      75
    );
    this.closePhonebook();
  }

  onSelecteItem() {
    let isBlind = false;
    let isConference = false;
    if (this.state.selected)
      this.props.transferRequest(
        this.props.endpoint.defaultCampaign,
        this.props.endpoint.name,
        null,
        this.state.selected.ExtNumber,
        this.state.selected.name,
        isBlind,
        isConference,
        75
      );
  }

  closePhonebook() {
    this.props.closePhonebook();
  }

  callback(key) {
    if (key === "2") this.getQueues();
  }

  render() {
    return (
      <>
        <Modal
          title="انتقال تماس"
          visible={this.props.visible}
          onOk={this.onSelecteItem}
          onCancel={() => this.closePhonebook()}
          okText="انتخاب"
          cancelText="انصراف"
          width={400}
          footer={[
            <a
              onClick={() => this.closePhonebook()}
              className="btn btn-sm btn-secondary"
              key="back"
              style={{ color: "#fff", marginLeft: 10 }}
              onClick={this.handleCancel}
            >
              بستن
            </a>,
            <a
              className="btn btn-sm btn-primary"
              key="submit"
              type="primary"
              style={{ color: "#fff" }}
              onClick={() => this.onSelecteItem()}
            >
              انتخاب
            </a>,
            ,
          ]}
        >
          <Tabs
            defaultActiveKey="1"
            type="card"
            onChange={(e) => this.callback(e)}
          >
            <TabPane tab="ایجنت ها" key="1">
              <div style={{ height: 350, overflow: "auto" }}>
                <table
                  class="table table-bordered "
                  style={{
                    borderColor: "rgb(238,238,238)",
                  }}
                >
                  <tbody>
                    {Object.values(this.props.phonebook).map((agent) => (
                      <tr>
                        <td
                          className="d-flex"
                          onClick={() => this.setState({ selected: agent })}
                          onDoubleClick={() => this.onSelectedAgent(agent)}
                        >
                          <Avatar
                            style={{
                              color: "#f56a00",
                              backgroundColor: "#fde3cf",
                            }}
                          >
                            {agent.DisplayName[0]}
                          </Avatar>
                          <p style={{ marginTop: 4, marginRight: 6 }}>
                            {agent.DisplayName} - {agent.ExtNumber}
                          </p>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </TabPane>
            <TabPane tab="صف ها" key="2">
              <div style={{ height: 350, overflow: "auto" }}>
                <table
                  class="table table-bordered "
                  style={{ borderColor: "rgb(238,238,238)" }}
                >
                  <thead>
                    <tr>
                      <th
                        style={{ borderColor: "rgb(238,238,238)" }}
                        scope="col"
                      >
                        نام
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.queues.map((queue, index) => {
                      return (
                        <td
                          className="d-flex"
                          onClick={() => this.setState({ selected: queue })}
                          onDoubleClick={() => this.onSelectedQueue(queue)}
                        >
                          <p style={{ marginTop: 4, marginRight: 6 }}>
                            {queue.name}
                          </p>
                        </td>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </TabPane>
          </Tabs>
        </Modal>
      </>
    );
  }
}

export default Phonebook;
