import React, { useState } from "react";
import * as signalR from "@microsoft/signalr";

var agentDesktopServer = new signalR.HubConnectionBuilder();
var connected = false;
var turretAdded = false;
var unloaded = true;
var authtoken = "";

class Agent extends React.Component {
  constructor(config) {
    super();
    authtoken = config.token;
    const baseUrl = config.server;
    const tenant = config.tenant;
    agentDesktopServer = new signalR.HubConnectionBuilder()
      .withUrl(
        `${baseUrl}/softdial/agentdesktop-server/${tenant}/agentDesktopServer`
      )
      .configureLogging(signalR.LogLevel.Information)
      .build();
  }
  componentDidMount() {
    unloaded = false;
  }
  connect(checkstate) {
    this.checkstate = checkstate;
    this.eventlistner();
    agentDesktopServer.start().then(this.from.onConnected);
  }
  close() {
    agentDesktopServer.stop();
  }
  
  eventlistner() {
    //This is for auto reconnect
    agentDesktopServer.onreconnecting((error) => {
      console.log("agentDesktopServer is reconnecting");
      if (error)
        console.error("agentDesktopServer reconnecting error " + error);

        connected = false;
        if(this.checkstate)
          this.checkstate(connected);
      // cc.desktop.ui.showWebsocketReconnectionBar();
    });

    agentDesktopServer.onreconnected((connectionId) => {
        console.log("agentDesktopServer is reconnected with id " + connectionId);
        this.from.onConnected();
    });

    agentDesktopServer.onclose((e) => {
      connected = false;
      this.checkstate(connected);
      this.onClose(e);
    });

    //agentDesktopServer.stateChanged(function (change) {
    //    cc.ws.onConnStateChanged(change);
    //});

    //agentDesktopServer
    //-------------------------------------------------------------------
    agentDesktopServer.on("creditError",  () =>{
      console.error("your company has no credit to use system");
      // etraab.lib.errorMessage(cc.messages.fa.No_Credit_Error);
      //cc.desktop.ui.updateCompanyCreditStatus(false);
    });
    agentDesktopServer.on("authorizeError",  ()=> {
      console.error("not authenticated for the agent desktop request");
      // etraab.lib.errorMessage(cc.messages.fa.Authorize_Error);
    });
    agentDesktopServer.on("warn",  (message) =>{
      console.warn(message);
    });
    agentDesktopServer.on("ack",  (message, retryAction) =>{
      console.error(message);
      if (retryAction)
        setTimeout(function () {
          // cc.ws[retryAction]();
        });
    });
    agentDesktopServer.on("setCookie",(d) => this.from.setCookie(d));
    //agentDesktopServer.on("setCampaign",  this.from.setCampaign);
    agentDesktopServer.on("onlineAgents", (s) => {
      //console.log(s);
    });

    agentDesktopServer.on("turretStatus", (rs,os) => this.from.turretStatus(rs,os));
  }

  onClose(error){
    console.error("websocket disconnected => " + error);   
    this.connect();
  }

  on(eventName, func) {
    agentDesktopServer.on(eventName, func);
  }

  to = {
    authorize: () => {
      agentDesktopServer.invoke("authorize", authtoken).catch(function (err) {
        console.error(err.toString());
      });
    },
    keepMeAlive: () => {
      agentDesktopServer.invoke("keepMeAlive").catch(function (err) {
        console.error(err.toString());
      });
    },
    sendAddTurret: () => {
      console.debug("AddTurret Message Sent");
      try {
         
          agentDesktopServer.invoke("addTurret").catch(function (err) {
            console.error(err.toString());
          });

          // this.to.keepMeAlive();
          // setTimeout(() => {
          //     if (!turretAdded)
          //      this.to.sendAgentLogin();

          //     turretAdded = true;
          // }, 2000);
          return true;
      } catch (e) {
          return false;
      }
    },
    sendAgentLogin: () => {
      try {
        console.log("Agent Login Message Sent");
        agentDesktopServer.invoke("agentLogin").catch(function (err) {
          console.error(err.toString());
        });
        return true;
      } catch (e) {
        setTimeout(function () {
          agentDesktopServer.invoke("agentLogin").catch(function (err) {
            console.error(err.toString());
          });
        }, 2000);
        return false;
      }
    },
    sendDeleteTurret: () => {
      console.debug("DeleteTurret Message Sent");
      try {
          agentDesktopServer.invoke("deleteTurret").catch(function (err) {
            console.error(err.toString());
          });
          return true;
      } catch (e) {
          return false;
      }
    },
    allEndpointsUnavailable: (reason, description) => {
      console.log("Agent allEndpointsUnavailable Message Sent");

      agentDesktopServer
        .invoke("agentUnAvailable", reason, description)
        .catch(function (err) {
          console.log(err.toString());
        });
    },
    endpointUnavailable: (ep, reason, description) => {
      console.log("Agent Endpoint " + ep + " RequestUnavailable Message Sent");
      agentDesktopServer
        .invoke("endpointUnavailable", ep, reason, description)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    allEndpointsAvailable: () => {
      console.log("Agent Available Message Sent");
      agentDesktopServer.invoke("agentAvailable").catch(function (err) {
        console.error(err.toString());
      });
    },
    endpointAvailable: (ep) => {
      console.log("Agent Endpoint " + ep + " Available Message Sent");
      agentDesktopServer.invoke("endpointAvailable", ep).catch(function (err) {
        console.error(err.toString());
        throw err;
      });
    },
    startWrap: (ep) => {
      console.debug("Agent StartWrapping Message Sent");
      agentDesktopServer.invoke("startWrap", ep).catch(function (err) {
        console.error(err.toString());
      });
    },
    transactionComplete: (ep, camp, oc) => {
      console.debug("Agent TransactionComplete Message Sent");
      if(!oc)
        oc = 0;
      agentDesktopServer
        .invoke("transactionComplete", camp, ep, oc)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    requestHold: (ep, goHold) => {
      console.debug("Agent RequestHold Message Sent");
      agentDesktopServer
        .invoke("requestHold", ep, goHold)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    requestMute: (ep, goMute) => {
      console.debug("Agent RequestMute Message Sent");
      agentDesktopServer
        .invoke("requestMute", ep, goMute)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    makeCall: (campaign, ep, number, prefix, isWaiting) => {
      console.debug("make call to " + number + " on endpoint " + ep);
      if (isWaiting === undefined) isWaiting = false;
      agentDesktopServer
        .invoke("makeCall", campaign, ep, number, prefix, isWaiting)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    getPhonebook: () => {
      console.debug("Get PhoneBook Sent");
      agentDesktopServer.invoke("phoneBook").catch(function (err) {
        console.error(err.toString());
      });
    },
    sendTransferRequest: (
      campaign,
      endpoint,
      targetEndpoint,
      external,
      targetGroup,
      blind,
      isConference,
      ringTimeout
    ) => {
      console.debug("TransferRequest Sent");

      if (!ringTimeout) ringTimeout = 12;

      if (!blind) blind = false;

      if (!isConference) isConference = false;

      agentDesktopServer
        .invoke(
          "transferRequest",
          campaign,
          endpoint,
          targetEndpoint,
          external,
          targetGroup,
          ringTimeout,
          blind,
          isConference
        )
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    sendTransferAcknowledge: (campaign, endpoint, rs) => {
      console.debug("TransferAcknowledge Sent");
      agentDesktopServer
        .invoke("transferAcknowledge", campaign, endpoint, rs)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    beginDial: (campaign, endpoint, dontDial, agentOutcome) => {
      console.debug("BeginDial Sent");
      agentDesktopServer
        .invoke("beginDial", campaign, endpoint, dontDial, agentOutcome)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    getCallWaitingCall: (parkQueue, sessionId, campaign, agent) => {
      console.debug("getCallWaitingCall Sent");
      agentDesktopServer
        .invoke("getCallWaitingCall", parkQueue, sessionId, campaign, agent)
        .catch(function (err) {
          console.error(err.toString());
        });
    },
    sendChatMessage: function (obj) {
      agentDesktopServer.invoke("sendChatMsg", {
            EP: obj.ep,
            SI: obj.sessionId,
            From: obj.from,
            To: obj.to,
            DateTimeStr: obj.date,
            SysMsg: obj.sysMessage,
            Body: obj.body
        }).catch(function (err) {
          console.error(err.toString());
        });
    },
    setAgentTypingState:(cn, ep, si, enabled)=>{
        agentDesktopServer.invoke("setAgentTypingState", cn, ep, si, enabled).catch(function (err) {
         console.error(err.toString());
        });
    },
    sv: {
        requestListen: function (campaign, ep, ep2, on, coach, bargeIn) {
            console.log("Agent RequestListen Message Sent");
            agentDesktopServer.invoke("requestListen", campaign, ep, ep2, on, coach, bargeIn).catch(function (err) {
                console.error(err.toString());
            });
        },
        hangupCall: function (campaign, ep) {
            console.log("Agent " + ep + " HangUpCall Message Sent");
            agentDesktopServer.invoke("hangupCall", campaign, ep).catch(function (err) {
                console.error(err.toString());
            });
        },
        logoutAgent: function (agent, campaign, endpoint) {
            agentDesktopServer.invoke("svLogout", agent, campaign, endpoint).catch(function (err) {
                console.error(err.toString());
            });
        },
        goUnavailable: function (agent, campaign, endpoint, reason, description) {
            agentDesktopServer.invoke("svUnavailable", agent, campaign, endpoint, reason, description).catch(function (err) {
                console.error(err.toString());
            });
        },
        goAvailable: function (agent, endpoint) {
            agentDesktopServer.invoke("svAvailable", agent, endpoint).catch(function (err) {
                console.error(err.toString());
            });
        },
        groupMembership: function (campaign, endpoint, queue, on) {
            agentDesktopServer.invoke("svGroupMembership", campaign, endpoint, queue, on).catch(function (err) {
                console.error(err.toString());
            });
        },
        moveCampaign: function (campaign, endpoint, campaign2, keepHome, blendMove) {
            agentDesktopServer.invoke("svMoveCampaign", campaign, endpoint, campaign2, keepHome, blendMove).catch(function (err) {
                console.error(err.toString());
            });
        },
    }
    
  };

  from = {
    onConnected: () => {
      console.log("agent-desktop server is connected");
      connected = true;
      try {
        this.checkstate(connected);
        this.to.authorize();
        setTimeout(() => {
          this.to.sendAddTurret();
          setTimeout(() => {
            this.to.keepMeAlive();
            setTimeout(() => {
              this.to.sendAgentLogin();
            }, 150);
          }, 150);
        }, 50);      
      } catch (e) {
        console.error("error on connected event. ",e);
      }
    },

    setCookie(data) {
      //etraab.lib.cookie.set(data.name, data.data, 1);
    },
    otherUserLoggedIn: () => {
      try {
        // etraab.lib.errorMessage(
        //   "کاربر گرامی به دلیل اتصال به کاربر جاری از سیستمی دیگر دسترسی شما قطع میگردد"
        // );
        //cc.ws.signOutUser();
      } catch (e) {
        // etraab.lib.errorMessage(
        //   "کاربر گرامی به دلیل اتصال به کاربر جاری از سیستمی دیگر دسترسی شما قطع میگردد"
        // );
      }

      //cc.ws.disconnected = true;
    },
    turretStatus: (resourceState, oldState) => {
      console.info(`turret status new state:${resourceState} | old state ${oldState}`);
      //0 => Turret deleted or does not exist.(OldState & RS)
      //1 => Turret Logged In and idle.(OldState & RS)
      if (!(turretAdded) && oldState === 0 && resourceState === 1) {
        this.to.sendAgentLogin();
      }
    },
  };
}

export default Agent;
