  LoginToContactCenter() {
    this.axioPostRequest(`Api/v1/cm/AgentDesktop/login`, {},(res) => {
        if (res.status == 200) {
          console.log("Login Agent Sucssesful", res);
          this.store.dispatch(
      			actions.setAgentData(res.data));
          this.agentdata = res.data;
          this.GetBreakReasons();
          this.GetProviders();
          this.init(res.data);
        }
      });
  }

  GetBreakReasons() {
    this.axioGetRequest('Api/v1/cm/BreakReason',(res) =>{
        console.log("break reasen ", res);
        this.store.dispatch(
          actions.setBreakReasons(res.data));
    });
  }

  GetProviders() {
    this.axioGetRequest('Api/v1/cm/agentdesktop/providers',(res) =>{
        console.log("media providers ", res);
        this.store.dispatch(
          actions.setProviders(res.data));

        this.providers = res.data
          .filter((p) => p.settingType === "Dnis")
          .map((x) => ({ text: x.settingTitle, value: x.no }));
        if (this.providers.length > 0) {
          this.store.dispatch(
            actions.setSelectedDnis(this.providers[0].value));
        }
    });
  }

  axioGetRequest(path,success) {
    axios
    .get(`${this._url_base}/${path}`, {
      headers: { Authorization: `Bearer ${this._token}` },
    })
    .then((res) => success(res))
    .catch((error) => {
      // this.error_handle("مشکلی در ارتباط با سرور به وجود امده است");
      return error;
    });
  }

  axioPostRequest(path,body,success) {
    axios
    .post(`${this._url_base}/${path}`, body, {
      headers: {
        Accept: "application/json",
        "Content-type": "application/json; charset=UTF-8",
        Authorization: `Bearer ${this._token}`,
      }
    })
    .then((res) => success(res))
    .catch((error) => {
      // this.error_handle("مشکلی در ارتباط با سرور به وجود امده است");
      return error;
    });
  }
  
