  connectToAgentDesktopServer(agentdata) {
    ws = new Agent(agentdata);

    //this is used for preventing any component did mount after starting for the first time 
    this.started = true;
    ws.on("agentLoggedIn", this.from.agentLoggedIn);
    ws.on("agentAwaitingStationTieUp", this.from.waitingTelephony);
    ws.on("agentOffline", this.from.agentOffline);
    ws.on("agentRequestUnAvailable", this.from.agentRequestUnAvailable);
    ws.on("agentConnected", this.from.agentConnected);
    ws.on("agentTalking", this.from.agentTalking);
    ws.on("agentFree", this.from.agentFree);
    ws.on("agentReady", this.from.agentReady);
    ws.on("agentNowAvailable", this.from.agentNowAvailable);
    ws.on("agentMakingFollowUpCall", this.from.agentMakingFollowUpCall);
    ws.on("agentNowLoggedOut", this.from.agentNowLoggedOut);
    ws.on("transferConnected", this.from.transferConnected);
    ws.on("transferDisConnected", this.from.transferDisConnected);
    ws.on("telephonyLayerNotRespond", this.from.telephonyLayerNotRespond);
    ws.on("transferConnectedConferencing",this.from.transferConnectedConferencing);
    ws.on("agentCallOffered", this.from.agentCallOffered);
    ws.on("inWrapMode", this.from.goWrapMode);
    ws.on("callEnded", this.from.callEnded);
    ws.on("sessionEnded", this.from.sessionEnded);
    ws.on("newChatMessage", this.from.chat.newMessageReceived);
    ws.on("newFax", this.from.newFax);
    ws.on("setCustomerTypingState", this.from.chat.setCustomerTypingState);
    ws.on("phoneBookResult", this.from.onPhoneBookResult);
    ws.on("newEmail", this.from.newEmail);
    ws.on("replyResult", this.from.replyResult);
    ws.on("handleSessionEvent", this.from.handleSessionEvent);
    ws.on("otherUserLoggedIn", this.from.otherUserLoggedIn);
    ws.on("processCallTransfer", this.from.processCallTransfer);
    ws.on("monitoringOtherAgent", this.from.monitoringOtherAgent);
    ws.on("monitorStarted", this.from.monitorStarted);
    ws.on("monitorStopped", this.from.monitorStopped);
    ws.on("monitorError", this.from.monitorError);
    ws.on("addPriorityCall", this.from.addPriorityCall);
    ws.on("removePriorityCall", this.from.removePriorityCall);
    ws.on("addParkedCall", this.from.addParkedCall);
    ws.on("removeParkedCall", this.from.removeParkedCall);
    ws.on("beginPreview", this.from.beginPreview);
    ws.on("onlineAgents", (args1) => {});
    ws.on("holdUpdate", (ep, on) => {
      if (on) this.from.goHold(ep);
      else this.from.goUnHold(ep);
    });
    ws.on("muteUpdate", (ep, on) => {
      if (on) this.from.goMute(ep);
      else this.from.goUnMute(ep);
    });

    ws.connect((connected) => this.store.dispatch(
        actions.setAgentSocketStatus(connected)));
  }

