  chat = {
    addNewAgentMessage: (sessionId, message) => {
      let index = this.ui.findSessionIndex(sessionId);
      if (index >= 0) {
        let session = this.store.getState().sessions[index];
        if (!session.data.messages)
          session.data = {
            messages: [],
          };

        session.data.messages.push(message);
        this.ui.updateSessionData(index, session);
        this.chat.sendAgentMessage(
          session.endpoint,
          sessionId,
          session.number,
          message.text,
          message.sysMessage
        );
      }
    },
    sendAgentMessage: (endpoint, sessionId, number, text, isSystemMessage) => {
      ws.to.sendChatMessage({
        //must be true to send a file
        sysMessage: isSystemMessage,
        ep: endpoint,
        date: this.ui.formatDateAMPM(new Date()),
        body: text,
        sessionId: sessionId,
        from: endpoint,
        to: number,
      });
    },
    setAgentTypingState: (cn, ep, si, enabled) => {
      ws.to.setAgentTypingState(cn, ep, si, enabled);
    },
    uploadFile:(session,file) => {
      let formdata = new FormData();
  
      formdata.append("file", file);
      formdata.append("SessionId", session.sessionId);
  
      axios
        .post(
          `${this._url_base}/Api/v1/cm/AgentDesktop/chat/uploadattachment`,
          formdata,
          {
            headers: {
              Authorization: `Bearer ${this._token}`,
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((res) => {
          // this.setState({
          //   fileupload: false,
          // });
          if (res && res.status === 204) {
            // this.setState({
            //   fileupload: false,
            // });
            this.props.rcocClient.chat.addNewAgentMessage({
              endpoint: session.endpoint,
              type: this.messageType,
              text: "file/" + file.name,
              date: this.ui.formatDateAMPM(new Date()),
              no: session.endpoint,
              //sysMessage must be true for the file message
              sysMessage: true,
              fromCustomer: false,
              unread: false,
            });
          }else{
            // this.setState({
            //   fileupload: false,
            // });
          }
        })
        .catch((error) => {
          console.error(error);
          return error;
        });

    }
  };

  email = {
    openAtc: (atc) => {
      window.open(atc, '_blank');
    },
    downloadAtc: (atc) => {
      window.open(atc, '_blank');
    },
    reply: (session,body,atcs) => {
      let replyMessage = {};
      replyMessage["Body"] = body;
      replyMessage["Subject"] = session.data.Subject;
      replyMessage["Campaign"] = session.campaign;
      replyMessage["Tenant"] = session.data.Tenant;
      replyMessage["Endpoint"] = session.endpoint;
      replyMessage["AgentId"] = session.endpoint;
      replyMessage["QueueName"] = session.queue;
      replyMessage["SessionId"] = session.sessionId;
      replyMessage["From"] = session.data.To;
      replyMessage["To"] = session.data.From;
      replyMessage["CC"] = session.data.Cc;
      replyMessage["AttachmentsPath"] = session.data.AttachmentsPath;
      replyMessage["AttachmentsUrl"] = session.data.AttachmentsUrl;
      if (atcs) replyMessage["Attachments"] = atcs.filter(x=>x.upload === true);
      //TODO
      replyMessage["BCC"] = "";
      this.axioPostRequest(`Api/v1/cm/AgentDesktop/email/reply`,{}, (res) => {
        if (res.status == 200) {
          console.log("replay email was sucssesful:", res);
        }
      });
    },
    uploadFile:(file,session) => {  
      let formdata = new FormData();
      formdata.append("file", file);
      formdata.append("emailFolderPath", session.data.AttachmentsPath);
      formdata.append("emailFolderUrl", session.data.AttachmentsUrl);
  
      axios
        .post(
          `${this._url_base}/Api/v1/cm/AgentDesktop/email/uploadattachment`,
          formdata,
          {
            headers: {
              Authorization: `Bearer ${this._token}`,
             "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((res) => {
          console.log("updalod email attachment file response is ", res);
          if (res && res.status === 200 && res.data.success) {
            this.email.addNewUploadedFileToAttachments(session.sessionId,res.data);
          }
        })
        .catch((error) => {
          console.error(error);
          return error;
        });
    },
    addNewUploadedFileToAttachments:(sessionId,file)=> {
      let index = this.ui.findSessionIndex(sessionId);
      if(index !== -1){
        let session = this.store.getState().sessions[index];
        if(!session.atcs)
         session.atcs = [];
        session.atcs = [...session.atcs, {name:file.fileName,url:file.desUrl,upload:true}];
        this.ui.updateSessionData(index, session);
      }
    },  
    re_addNewUploadedFileToAttachments:(sessionId,atcIndex)=> {
      let index = this.ui.findSessionIndex(sessionId);
      if(index !== -1){
        let session = this.store.getState().sessions[index];
        session.atcs[atcIndex].upload = true;
        this.ui.updateSessionData(index, session);
      }
    }, 
    removeFromFileFromAttachments:(sessionId,atcIndex) => {
      let index = this.ui.findSessionIndex(sessionId);
      if(index !== -1){
        let session = this.store.getState().sessions[index];
        session.atcs[atcIndex].upload = false;
        this.ui.updateSessionData(index, session);
      }
    }
  }

