const mapState = (state) => {
  return {
    url_base: state.url_base,
    agentdata: state.agentdata,
    activeSipSession: state.activeSipSession,
    webrtcConnected: state.webrtcConnected,
    agentWsConnected: state.agentWsConnected,
    providers: state.providers,
    sessions: state.sessions,
    selectedDnis: state.selectedDnis,
    break_reasons: state.break_reasons,
    adhoc_number: state.adhoc_number,
  };
};
const mapDis = (dispatch) => {
  return {
    setAgentData: (item) => dispatch(actions.setAgentData(item)),
    setSessions: (items) => dispatch(actions.setSessions(items)),
    setBreakReasons: (item) => dispatch(actions.setBreakReasons(item)),
    setProviders: (item) => dispatch(actions.setProviders(item)),
    setAgentSocketStatus: (item) =>
      dispatch(actions.setAgentSocketStatus(item)),
    setWebRtcStatus: (item) => dispatch(actions.setWebRtcStatus(item)),
    setSelectedDnis: (item) => dispatch(actions.setSelectedDnis(item)),
    changeNavigate: (item, state) =>
      dispatch(actions.changeNavigate(item, state)),
    setAdhocNumber: (item, state) =>
      dispatch(actions.setAdhocNumber(item, state)),
  };
};

export default withRouter(connect(mapState, mapDis)(Dial));
