import React from "react";
import { Route, Link, Redirect, withRouter } from "react-router-dom";
import { Image, StyleSheet, Text, View, TextInput } from "react-native";
import { Tabs, Popover, Button, Popup } from "antd-mobile";
import { connect } from "react-redux";
import axios from "axios";
var endpointTabPrefix = "EP_Tab_";

class EndPoints extends React.Component {
  state = {
    visible: false,
    ep: null,
  };
  handle_popup(ep) {
    console.log(ep);
    this.setState({ visible: !this.state.visible, ep: ep });
  }
  render() {
    const { rcocClient } = this.props;
    return (
      <section style={{ height: "100vh", backgroundColor: "#fff" }}>
        <Popup
          visible={this.state.visible}
          onMaskClick={() => this.setState({ visible: !this.state.visible })}
          bodyStyle={{ minHeight: "40vh" }}
        >
          <div align="right">
            <div
              style={{
                height: 40,
                borderRadius: 6,
                marginTop: 8,
                borderBottom: "1px solid #ccc",
              }}
            >
              <a
                onClick={() =>
                  rcocClient.commands.endpointAvailable(this.state.ep.name)
                }
                class="dropdown-item"
                href="#"
              >
                در انتظار تماس
              </a>
            </div>
            {this.props.break_reasons?.map((item, index) => {
              return (
                <div
                  style={{
                    height: 40,
                    borderRadius: 6,
                    marginTop: 8,
                    borderBottom: "1px solid #ccc",
                  }}
                >
                  <a
                    onClick={() =>
                      rcocClient.commands.endpointUnAvailable(
                        this.state.ep.name,
                        item.name,
                        item.title
                      )
                    }
                    class="dropdown-item"
                    href="#"
                  >
                    {item.title}
                  </a>
                </div>
              );
            })}
          </div>
        </Popup>
        <section
          style={{
            backgroundColor: "#6f0f4f",
            height: this.props.callstate !== "" ? 120 : 100,
          }}
        >
          <div class="container">
            <div class="row">
              <div class="col-md-12" align="right">
                <h1
                  style={{
                    fontSize: 24,
                    marginTop: this.props.callstate !== "" ? 40 : 20,
                    marginRight: 6,
                    fontFamily: "Vazir",
                    color: "#fff",
                  }}
                >
                  درگاه ها
                </h1>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12" align="right">
                <div class="d-flex" style={{ flexDirection: "row-reverse" }}>
                  <div
                    style={{
                      height: 30,
                      marginLeft: 10,
                      borderRadius: 30,
                      width: "auto",
                      backgroundColor: "#561042",
                    }}
                  >
                    <h6
                      style={{
                        fontFamily: "Vazir",
                        color: "#fff",
                        paddingLeft: 20,
                        paddingRight: 20,
                        marginTop: 6,
                        fontSize: 13,
                      }}
                    >
                      لیست درگاه های موجود
                    </h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section>
          <div class="container">
            <div class="row">
              <div class="col-md-12">
                {this.props.agentdata.endpoints.map((itemx, index) => {
                  return (
                    <div
                      class="card "
                      style={{
                        borderRadius: 6,
                        height: 100,
                        margin: 10,
                      }}
                    >
                      <div style={{ marginTop: 12, marginRight: 15 }}>
                        <p
                          style={{ fontSize: 14 }}
                          class="d-flex justify-content-between"
                        >
                          <span
                            style={{
                              color: "green",
                              marginTop: 10,
                            }}
                          >
                            <Button
                              onClick={() => this.handle_popup(itemx)}
                              // color="success"
                              style={{
                                fontFamily: "Vazir",
                                marginLeft: 10,
                                marginTop: 5,
                              }}
                            >
                              وضعیت
                            </Button>
                          </span>
                          <div
                            align="right"
                            style={{ flexDirection: "column" }}
                            id={endpointTabPrefix + itemx.name}
                          >
                            <h6 class="name" style={{ fontSize: 13 }}>
                              {itemx.name}
                            </h6>
                            <h6
                              class="number"
                              style={{ fontSize: 14, fontFamily: "Vazir" }}
                            >
                              {itemx.number && itemx.number !== ""
                                ? itemx.number
                                : "بدون اتصال"}
                            </h6>
                            <h6
                              class="campaign"
                              style={{ fontSize: 10, marginTop: 4 }}
                            >
                              {itemx.defaultCampaign}
                            </h6>
                            <h6
                              class="state"
                              style={{
                                fontFamily: "Vazir",
                                fontSize: 10,
                                marginTop: 4,
                                marginBottom: 15,
                                color: itemx.status
                                  ? itemx.status.color
                                  : "black",
                              }}
                            >
                              {itemx.status
                                ? itemx.status.text
                                : "سرویس متصل است"}
                            </h6>
                          </div>
                        </p>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </section>
      </section>
    );
  }
}

const mapState = (state) => {
  return {
    url_base: state.url_base,
    agentdata: state.agentdata,
    callstate: state.callstate,
    break_reasons: state.break_reasons,
  };
};
const mapDis = (dispatch) => {
  return {};
};

export default withRouter(connect(mapState, mapDis)(EndPoints));
