import React from "react";
import Timer from "../../Timer";
import { Popup } from "antd-mobile";

class IncomingVoiceCallWindow extends React.Component {
  timer;

  componentDidMount() {
    if (!this.props.session) this.lastState = this.props.session.state;
    let timer = document
      .getElementById("incomingCallPopupContainer")
      .querySelector('h6[name="dur"]');
    this.timer = new Timer(timer);
    this.timer.start();
  }

  componentWillUnmount() {
    try {
      this.timer.stop();
    } catch (e) {
      console.error(e);
    }
  }

  componentDidUpdate() {
    this.timer.start();
  }

  render() {
    return (
      <Popup
        id="incomingCallPopupContainer"
        visible={true}
        // onMaskClick={() => this.setState({ visibleAction: true })}
        bodyStyle={{
          borderTopLeftRadius: "8px",
          borderTopRightRadius: "8px",
          minHeight: "20vh",
        }}
      >
        <div class="m-2" align="right">
          <h6 style={{ fontFamily: "Vazir", color: "#ccc", marginTop: 10 }}>
            تماس ورودی
          </h6>
          <h5>
            {this.props.session?.number
              ? this.props.session.number
              : "تماس ورودی"}
          </h5>
          <h6 name="dur" style={{ color: "green", marginLeft: 17 }}>
            00:00:00
          </h6>
        </div>
        <hr />
        <div class="d-flex justify-content-between m-1">
          <a
            class="btn  btn-danger"
            onClick={() => this.props.rejectCall(this.props.session)}
            style={{ width: "32%", fontFamily: "Vazir", color: "#fff" }}
          >
            رد کردن
          </a>
          <a
            class="btn btn-success"
            style={{ width: "32%", fontFamily: "Vazir", color: "#fff" }}
            onClick={() => this.props.acceptCall(this.props.session)}
          >
            پاسح دادن
          </a>
        </div>
      </Popup>
    );
  }
}

export default IncomingVoiceCallWindow;
