import React from "react";
import axios from "axios";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import "./style.css";
import InComingMesssage from "./Cards/InComingMesssage";
import SystemMessage from "./Cards/SystemMessage";
import OutgoingMesssage from "./Cards/OutgoingMesssage";
import FileMessage from "./Cards/FileMessage";
import { Tabs, Popup } from "antd-mobile";
class Chat extends React.Component {
  formatDateAMPM(datetime) {
    try {
      return datetime.toLocaleString("fa-IR");
    } catch (e) {
      return "";
    }
  }

  onSendMessageKeyUp(event) {
    if (
      !event.shiftKey &&
      event.which === 13 &&
      event.target.value.trim() !== ""
    ) {
      this.sendMessage(event.target.value);
      event.target.value = "";
    }

    if (!this.isTyping) {
      this.isTyping = true;
      this.setAgentTypingState(true);
      setTimeout(() => {
        this.isTyping = false;
        this.setAgentTypingState(false);
      }, 2000);
    }
  }

  setAgentTypingState(enabled) {
    let { session, rcocClient } = this.props;
    rcocClient.chat.setAgentTypingState(
      session.campaign,
      session.endpoint,
      session.sessionId,
      enabled
    );
  }

  onSendMessageButtonClicked(event) {
    let input = event.target.parentElement.querySelector('input[name="body"]');
    this.sendMessage(input.value);
    input.value = "";
  }

  sendMessage(text) {
    let { session } = this.props;
    let message = {
      endpoint: session.endpoint,
      type: this.messageType,
      text: text,
      date: this.formatDateAMPM(new Date()),
      no: session.endpoint,
      sysMessage: false,
      fromCustomer: false,
      unread: false,
    };

    this.addNewAgentMessage(message);
    this.isTyping = false;
    return;
  }

  onChangeUploadFile(e) {
    let { session, rcocClient } = this.props;
    this.setState({
      fileupload: true,
    });

    let file = e.target.files[0];
    rcocClient.chat.uploadFile(session, file);
  }

  addNewAgentMessage(message) {
    this.props.rcocClient.chat.addNewAgentMessage(
      this.props.session.sessionId,
      message
    );
  }

  onClickUploadAttachment(e) {
    let input = e.target.parentElement.querySelector(
      'input[name="ChatFileUpload"]'
    );
    input.click();
  }

  chatStates = {
    typing: "1",
    away: "2",
  };
  state = {
    popup: false,
  };

  render() {
    let { session, rcocClient } = this.props;
    return (
      <>
        <Popup
          visible={this.state.popup}
          onMaskClick={() => {
            this.setState({ popup: !this.state.popup });
          }}
          bodyStyle={{
            borderTopLeftRadius: "8px",
            borderTopRightRadius: "8px",
            minHeight: "15vh",
          }}
        >
          <div class="d-flex justify-content-between m-1">
            <a
              class="btn  btn-danger"
              onClick={() => this.props.rejectCall(this.props.session)}
              style={{ width: "32%", fontFamily: "Vazir", color: "#fff" }}
            >
              رد کردن
            </a>
            <a
              class="btn btn-success"
              style={{ width: "32%", fontFamily: "Vazir", color: "#fff" }}
              onClick={() => this.props.acceptCall(this.props.session)}
            >
              پاسح دادن
            </a>
          </div>
        </Popup>
        <div class="container" style={{ background: "#fff" }}>
          <div class="row">
            <div class="col-md-12" align="center">
              <div class="chat">
                {(() => {
                  return (
                    <div class="messages" id="chat">
                      {session.data.messages?.map((message, index) => {
                        this.messageType = message.type;
                        if (message.sysMessage) {
                          if (message.text.startsWith("file/")) {
                            return (
                              <FileMessage
                                rcocClient={rcocClient}
                                message={message}
                              />
                            );
                          } else {
                            return <SystemMessage message={message} />;
                          }
                        } else {
                          if (message.fromCustomer) {
                            return <InComingMesssage message={message} />;
                          } else {
                            return <OutgoingMesssage message={message} />;
                          }
                        }
                      })}
                      {(() => {
                        if (session.typingState === this.chatStates.typing) {
                          return (
                            <div class="message stark">
                              <div class="typing typing-1"></div>
                              <div class="typing typing-2"></div>
                              <div class="typing typing-3"></div>
                            </div>
                          );
                        }
                      })()}
                    </div>
                  );
                })()}
              </div>
            </div>
          </div>
        </div>
        <div
          class="footer-menu "
          align="center"
          style={{
            height: 70,
            backgroundColor: "#fff",

            borderTop: "1px solid #eee",
            borderBottom: "1px solid #eee",
            overflow: "hidden",
            position: "fixed",
            width: "100%",
            zIndex: 1,
            margin: "0px auto",
            bottom: 60,
          }}
        >
          <div class="chat">
            <div class="d-flex justify-content-between m-3">
              <div
                onClick={() => this.setState({ popup: true })}
                style={{
                  width: 30,
                  height: 30,

                  marginTop: 6,
                }}
              >
                <ion-icon
                  style={{ fontSize: 26, marginLeft: 0 }}
                  name="ellipsis-vertical-outline"
                ></ion-icon>
              </div>

              <ion-icon
                onClick={(e) => this.onClickUploadAttachment(e)}
                style={{
                  fontSize: 28,
                  marginLeft: 10,
                  marginRight: 10,
                  marginTop: 6,
                }}
                name="attach-outline"
              ></ion-icon>
              <input
                type="file"
                onChange={(e) => this.onChangeUploadFile(e)}
                name="ChatFileUpload"
                hidden
              />

              <input
                name="body"
                class="chat-input-input"
                onKeyUp={(e) => this.onSendMessageKeyUp(e)}
                style={{
                  fontFamily: "Vazir",
                  textAlign: "right",
                  minWidth: 250,
                }}
                placeholder="پیام خود را اینجا بنویسید"
                type="text"
              />
              <ion-icon
                style={{ fontSize: 28, marginTop: 6 }}
                name="send-outline"
                onClick={(e) => this.onSendMessageButtonClicked(e)}
              ></ion-icon>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapState = (state) => {
  return {
    url_base: state.url_base,
  };
};
const mapDis = (dispatch) => {
  return {};
};

export default withRouter(connect(mapState, mapDis)(Chat));
