import React from "react";
import { Button, Image, StyleSheet, Text, View, TextInput } from "react-native";
import { Tabs, Input, List } from "antd-mobile";

class ChatNew extends React.Component {
  state = { smsadd: [] };
  handle_sms_customer_number_input(e) {
    this.setState({
      sms_customer_number: e.target.value,
    });
  }
  handle_sms_text(e) {
    this.setState({
      sms_text: e.target.value,
    });
  }
  handle_add_sms_number() {
    this.setState((prevState) => {
      let newData_list = [...prevState.smsadd];
      newData_list.push(this.state.sms_customer_number);
      return { smsadd: newData_list };
    });
  }
  handle_update_sms_number(index, e) {
    const ddd = this.state.smsadd;
    ddd[index] = e.target.value;
    this.setState({ smsadd: ddd });
  }
  handle_remove_sms_number(index) {
    this.setState((prevState) => {
      let newData_list = [...prevState.smsadd];
      newData_list.splice(index, 1);
      return { smsadd: newData_list };
    });
  }
  render() {
    return (
      <>
        <div class="container">
          <div class="row">
            <div class="col-md-12 mt-1" align="right">
              <div class="form-group">
                <label
                  for="exampleFormControlTextarea1"
                  style={{ fontFamily: "Vazir" }}
                >
                  ایمیل کاربر
                </label>
                <div class="d-flex">
                  <a
                    onClick={() => this.handle_add_sms_number()}
                    style={{
                      color: "#fff",
                      fontFamily: "Vazir",
                      backgroundColor: "#6f0f4f",
                      border: "1px solid #6f0f4f",
                      marginRight: 8,
                    }}
                    class="btn btn-primary btn-small"
                  >
                    +
                  </a>
                  <input
                    onChange={(e) => this.handle_sms_customer_number_input(e)}
                    class="form-control"
                    placeholder="araz2salmasi@gmail.com"
                    style={{ direction: "rtl", fontFamily: "Vazir" }}
                  />
                </div>
                {this.state.smsadd.map((item, index) => (
                  <div class="d-flex" style={{ marginTop: 5 }}>
                    <a
                      onClick={() => this.handle_remove_sms_number(index)}
                      style={{
                        color: "#fff",
                        fontFamily: "Vazir",

                        marginRight: 8,
                      }}
                      class="btn btn-danger btn-small"
                    >
                      X
                    </a>
                    <input
                      onChange={(e) => this.handle_update_sms_number(index, e)}
                      class="form-control"
                      defaultValue={item}
                      style={{ direction: "rtl", fontFamily: "Vazir" }}
                    />
                  </div>
                ))}
              </div>
              <div class="form-group">
                <label
                  for="exampleFormControlTextarea1"
                  style={{ fontFamily: "Vazir" }}
                >
                  متن پیام
                </label>
                <textarea
                  rows="6"
                  class="form-control"
                  style={{ fontFamily: "Vazir" }}
                ></textarea>
              </div>
              <div class="form-group">
                <div
                  style={{
                    backgroundColor: "#f8f8f8",
                    borderRadius: 6,
                    height: 50,
                    border: "1px solid #ccc",
                  }}
                ></div>
              </div>
            </div>
          </div>
        </div>
        <a
          onClick={() => this.setState({ tab: 3 })}
          style={{
            height: 60,
            width: 60,
            borderTop: "1px solid #eee",
            backgroundColor: "#6f0f4f",
            overflow: "hidden",
            borderRadius: 50,
            right: 15,
            position: "fixed",
            bottom: 150,
          }}
        >
          <ion-icon
            name="attach-outline"
            style={{
              color: "#fff",
              marginLeft: 15,
              marginTop: 14,
              fontSize: 30,
            }}
          ></ion-icon>
        </a>
        <div
          class="footer-menu"
          align="center"
          style={{
            height: 70,
            backgroundColor: "#6f0f4f",
            boxShadow: "0px 2px 21px 2px rgba(235,235,235,0.75)",

            borderTop: "1px solid #eee",

            overflow: "hidden",
            position: "fixed",
            width: "100%",

            margin: "0px auto",
            bottom: 50,
          }}
        >
          <a
            class="btn btn-small btn-primary btn-block"
            style={{
              marginTop: 6,
              color: "#fff",
              fontFamily: "Vazir",
              backgroundColor: "#6f0f4f",
              border: "1px solid #6f0f4f",
            }}
          >
            ارسال
          </a>
        </div>
      </>
    );
  }
}

export default ChatNew;
