const mapState = (state) => {
  return {
    url_base: state.url_base,
    agentdata: state.agentdata,
    navigate: state.navigate,
    callstate: state.callstate,
    webrtcConnected: state.webrtcConnected,
    agentWsConnected: state.agentWsConnected,
    providers: state.providers,
    sessions: state.sessions,
    break_reasons: state.break_reasons,
    activeChatSession: state.activeChatSession,
    incomingCall: state.incomingCall,
    activeEmailSession: state.activeEmailSession,
  };
};
const mapDis = (dispatch) => {
  return {
    setAgentData: (item) => dispatch(setAgentData(item)),
    setSessions: (items) => dispatch(setSessions(items)),
    setBreakReasons: (item) => dispatch(setBreakReasons(item)),
    setProviders: (item) => dispatch(setProviders(item)),
    setAgentSocketStatus: (item) => dispatch(setAgentSocketStatus(item)),
    setWebRtcStatus: (item) => dispatch(setWebRtcStatus(item)),
    changeNavigate: (item, state) => dispatch(changeNavigate(item, state)),
    setIncomingCall: (item, state) => dispatch(setIncomingCall(item, state)),
  };
};

export default withRouter(connect(mapState, mapDis)(App));
