import React from "react";
import { Route, Link, Redirect, withRouter } from "react-router-dom";
import { Spin } from "antd";
import { Input, notification } from "antd";
import { connect } from "react-redux";
import Fa_App from "./Fa/App";
import Ar_App from "./Ar/App";
import RcocClient from "./RcocClient";
import store from "../redux/store";

const lan = "Fa";
var token = localStorage.getItem("token");
class App extends React.Component {
  render() {
    let rcocClient = new RcocClient({
      token: token,
      url_base: this.props.url_base,
    });

    rcocClient.setStore(store);

    window.rcc = rcocClient;
    return (
      <>
        {(() => {
          if (lan === "Fa") {
            return <Fa_App rcocClient={rcocClient} />;
          } else if (lan === "Ar") {
            return <Ar_App rcocClient={rcocClient} />;
          }
        })()}
      </>
    );
  }
}

const mapState = (state) => {
  return {
    url_base: state.url_base,
  };
};
const mapDis = (dispatch) => {
  return {};
};

export default withRouter(connect(mapState, mapDis)(App));
