import React, { useState } from "react";

class AgentSipClient extends React.Component {
  constructor(debug, register) {
    super();
    this.debug = debug;
    this.register = register;
  }
  checks = {
    isSecure: false,
    hasMicrophone: false,
    hasSpeakers: false,
    isWebRTCSupported: false,
    isWebSocketsSupported: false,
  };
  config = {
    debug: this.debug || false,
    register: this.register || true,
  };
  sessionOptions = {
    media: {
      constraints: {
        audio: true,
        video: false,
      },
      render: {
        remote: document.getElementById("remoteAudio"),
      },
    },
  };
  on = function (event, fn) {
    this._events = this._events || {};
    this._events[event] = this._events[event] || [];
    this._events[event].push(fn);
  };
  // add event handler that executes once and then removes itself
  once = function (event, fn) {
    let fired = false;

    function g() {
      this.off(event, g);
      if (!fired) {
        fired = true;
        fn.apply(this, arguments);
      }
    }

    g.fn = fn;
    this.on(event, g);
  };
  // remove event handler
  off = function (event, fn) {
    this._events = this._events || {};
    if (event in this._events === false) {
      return;
    }
    this._events[event].splice(this._events[event].indexOf(fn), 1);
  };
  // emit event
  emit = function (event) {
    this._events = this._events || {};
    if (event in this._events === false) {
      return;
    }
    for (let i = 0; i < this._events[event].length; i++) {
      this._events[event][i].apply(
        this,
        Array.prototype.slice.call(arguments, 1)
      );
    }
  };
  // get listeners
  listeners = function (type) {
    return !type || !this._events || !this._events[type]
      ? []
      : this._events[type];
  };
  createRemoteAudio = function () {
    let a = document.createElement("audio");
    a.id = "remoteAudio";
    // this is responsible for feedback
    a.autoplay = false;
    document.body.appendChild(a);
    return a;
  };

  start = function (config) {
    //this.detect();

    let options = Object.assign(
      {
        userAgentString: "RokhCC - SIP.js/0.13.8 BB",
        //expiring time in seconds
        registerOptions: { expires: 300 },
        register: this.config.register === true,
      },
      config
    );

    this.ua = new window.SIP.UA(options);
    let me = this;
    this.ua.on("connected", function () {
      console.log("sip ua is connected to the server")
      me.emit("connected");
    });
    this.ua.on("disconnected", function () {
      me.emit("disconnected");
    });
    this.ua.on("registered", function () {
      console.log("sip ua is registered on server")
      me.emit("registered");
    });
    this.ua.on("unregistered", function (cause) {
      me.emit("unregistered", cause);
    });
    this.ua.on("registrationFailed", function (cause) {
      me.emit("registrationFailed", cause);
    });
    this.ua.on("invite", this.onInvite.bind(this));
    this.ua.on("message", function (msg) {
      me.emit("message", msg);
    });
    //this.ua.on('transportCreated', msg => this.emit('transportCreated', msg));
  };
  unRegister = function () {
    if (this.ua) this.ua.unregister();
  };
  onInvite = function (session) {
    console.log("onInvite new session:",session);
    //there is already a session on the window - just simply reject the new one
    // if (
    // //  cc.activeSession &&
    //   cc.activeSession.request.callId !== session.request.callId
    // ) {
    //   log.warn(
    //     "there is already a session on the window so rejected the new one"
    //   );
    //   return cc.phone.rejectCall(session);
    // }

    let me = this;
    let remoteAudio = this.createRemoteAudio();

    session.on("SessionDescriptionHandler-created", function () {
      let b = session.sessionDescriptionHandler;
      //session.sessionDescriptionHandler.options.peerConnectionOptions.rtcConfiguration.iceServers = [{ urls: "stun:79.175.170.164:3478" }];
      b.on("iceGatheringComplete", function () {
        if (this.iceGatheringTimeout) {
          console.error("ICE Server Timeout");
        }
      });
      b.on("iceConnectionFailed", function () {
        console.error("iceConnectionFailed", arguments);
      });
    });
    session.on("accepted", function () {
      me.emit("sessionAccepted", session);
    });
    session.on("rejected", function () {
      me.emit("sessionRejected", session);
      console.log("session is rejected :" + session);
    });
    session.on("cancel", function () {
      console.log("ContactCenterPhone SESSION canceled ");
      //cc.indicateSessionCancelled();
    });
    session.on("directionChanged", function () {
      var direction = session.sessionDescriptionHandler.getDirection();
      //   cc.desktop.ui.agentToAgentCall.indicateCallHold(
      //     !(direction === "sendrecv")
      //   );
    });
    session.on("failed", function () {
      me.emit("sessionFailed", session);
    });
    session.on("connecting", function () {
      me.emit("sessionConnecting", session);
    });
    session.on("trackAdded", function () {
      let pc = session.sessionDescriptionHandler.peerConnection;
      let remoteStream = new MediaStream();

      pc.getReceivers().forEach(function (receiver) {
        remoteStream.addTrack(receiver.track);
      });
      remoteAudio.srcObject = remoteStream;
      remoteAudio.play();
    });
    session.on("bye", function (request) {
      console.log("ContactCenterPhone Session Bye sent " + request);
    });

    session.on("terminated", function (message, cause) {
      console.log(
        "ContactCenterPhone SESSION TERMINATED " + message + " | " + cause
      );

      //   if (
      //     cc.agentToAgentCall &&
      //     cc.agentToAgentCall.request.callId === session.request.callId
      //   )
      //     cc.desktop.ui.agentToAgentCall.indicateCallTerminated();

      //   //an other call sends invite but cancelled when we have existing call
      //   if (
      //     cc.activeSession &&
      //     cc.activeSession.request.callId !== session.request.callId
      //   )
      //     return false;

      //   cc.desktop.ui.enableMakeCallBtn();
      //   cc.desktop.ui.showDialPadUserListCallBtns();

      //   if (cc.activeSession) {
      //     cc.activeSession = undefined;
      //   }
      
      me.emit("sessionTerminated", session);

      if (remoteAudio) {
        remoteAudio = undefined;
        //delete remoteAudio;
      }
    });
    this.emit("invite", session);
  };
  acceptCall = function (session) {
    let options = {
      sessionDescriptionHandlerOptions: {
        constraints: {
          audio: true,
          video: false,
        },
      },
    };
    session.accept(options);
  };
  rejectCall = function (session) {
    session.reject();
  };
  makeCall = function (number, constraints, extraHeaders) {
    //if (cc.agentToAgentCall || cc.activeSession) return;

    if (!constraints)
      constraints = {
        audio: true,
        video: false,
      };

    let session = this.ua.invite(number, {
      sessionDescriptionHandlerOptions: {
        constraints: constraints,
      },
      extraHeaders: extraHeaders,
    });

    return session;
  };
  toggleMute = function (session) {
    let pc = session.sessionDescriptionHandler.peerConnection;
    pc.getSenders().forEach((stream) => {
      stream.track.enabled = !stream.track.enabled;
      session.voice_muted = !stream.track.enabled;
    });
  };
  toggleHold = function (session) {
    if (session.holded) {
      session.unhold();
      session.holded = false;
    } else {
      session.hold();
      session.holded = true;
    }
  };
  endCall = function (session) {
    session.bye();
  };
  detect = function () {
    let me = this;
    window.DetectRTC.load(function () {
      me.checks = {
        //isSecure: window.location.protocol === location.protocol,
        hasMicrophone: window.DetectRTC.hasMicrophone,
        canUseMicrophone: window.DetectRTC.isWebsiteHasMicrophonePermissions,
        hasSpeakers: window.DetectRTC.hasSpeakers,
        isWebRTCSupported: window.DetectRTC.isWebRTCSupported,
        isWebSocketsSupported: window.DetectRTC.isWebSocketsSupported,
      };

      console.log(me.checks);
      //if (me.checks.canUseMicrophone !== cc.currentMicrophoneState) {
      // cc.currentMicrophoneState = me.checks.canUseMicrophone;
      // if (me.checks.canUseMicrophone === true) {
      //   log.info("Microphone connected");
      //   if (!cc.phoneConnected) {
      //     me.checks.start(cc.sipOptions);
      //   }
      // } else if (me.checks.canUseMicrophone === false) {
      //   //cc.indicateSessionTerminated();
      //   // if the user has webRTC option configured then check the protocol
      //   // as it works only on https connection
      //   if (cc.desktop.client.user.webrtc && location.protocol !== "https:") {
      //     console.error("Error => invalid protocol(use https)");
      //     cc.desktop.ui.confirmMessage(
      //       "لطفا از پروتکل https استفاده نمایید",
      //       function () {
      //         location.protocol = "https:";
      //       },
      //       null
      //     );
      //   } else {
      //     if (me.checks.hasMicrophone === false) {
      //       console.error("Error => please connect your microphone");
      //       etraab.lib.errorMessage("میکروفون شما متصل نمی باشد");
      //     } else {
      //       console.error("Error => permission is required for Microphone");
      //       etraab.lib.errorMessage(
      //         "لطفا به مرورگر اجازه دسترسی به میکروفون خود را بدهید"
      //       );
      //     }
      //   }
      // }
      //}

      if (me.hasSpeakers === false) {
        console.error("There is no speaker to play audio");
        // etraab.lib.errorMessage(
        //   "لطفا دستگاه اسپیکر خود را جهت پخش صدا بررسی نمایید"
        // );
      }
    });
  };
}
export default AgentSipClient;
