import * as types from "./type";

const initialState = {
  store_id: 6,
  user_id: 2,
  token: null,
  url_base: "https://erp.rokhcc.ir:90",
  url_base_SO: "http://79.175.170.164:89",
  url_base_sm: "https://79.175.170.164:86/Api/v1/",
  url_base_cm: "http://79.175.170.164:83",
  navigate: "Dial",
  callstate: "",
  rcocClient: null,
  webrtcConnected: false,
  agentWsConnected: false,
  agentdata: null,
  providers: null,
  selectedDnis: null,
  adhoc_number: "09015657617",
  break_reasons: null,
  activeSipSession: null,
  activeChatSession: null,
  activeEmailSession: null,
  sessions: [],
  userid: "",
  phonebook: {},
  incomingCall: null,
  popcall: false,
  popchat: false,
  popemail: false,
};
export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case types.CALL:
      return state;
    case types.CHANGE_NAVIGATE:
      console.log(action.payloadx, "action.payloadx");
      return {
        ...state,
        navigate: action.payload,
        callstate: action.payloadx,
        userid: action.payloady,
        props: action.payloadProps,
      };
    case types.SET_AGENTDATA:
      return {
        ...state,
        agentdata: action.payload,
      };
    case types.SET_PROVIDERS:
      return {
        ...state,
        providers: action.payload,
      };
    case types.SET_BREAKREASONS:
      return {
        ...state,
        break_reasons: action.payload,
      };
    case types.SET_AGENT_SOCKET_STATUS:
      return {
        ...state,
        agentWsConnected: action.payload,
      };
    case types.SET_WEBRTCSTATUS:
      return {
        ...state,
        webrtcConnected: action.payload,
      };
    case types.SET_SESSIONS:
      return {
        ...state,
        sessions: action.payload,
      };
    case types.SET_SELECTED_DNIS:
      return {
        ...state,
        selectedDnis: action.payload,
      };

    case types.SET_SIP_ACTIVE_SESSION:
      return {
        ...state,
        activeSipSession: action.payload,
      };
    case types.SET_AHOCNUMBER:
      return {
        ...state,
        adhoc_number: action.payload,
      };
    case types.SET_ACTIVECHATSESSION:
      return {
        ...state,
        activeChatSession: action.payload,
      };
    case types.SET_ACTIVEEMAILSESSION:
      return {
        ...state,
        activeEmailSession: action.payload,
      };
    case types.SET_INCOMINGCALL:
      return {
        ...state,
        incomingCall: action.payload,
      };
    case types.SET_PHONEBOOK:
      return {
        ...state,
        phonebook: action.payload,
      };

    case types.POP_CALL:
      return {
        ...state,
        popcall: action.payload,
      };
    case types.POP_CHAT:
      console.log(action.payload);
      return {
        ...state,
        popchat: action.payload,
      };
    case types.POP_EMAIL:
      return {
        ...state,
        popemail: action.payload,
      };

    default:
      return state;
  }
};
